import React, { Component } from 'react';
import styled from 'styled-components';
import projects from './ProjectsData';
import ImageArrow from './ImageArrow';


const ProjectDetailedContainer = styled.div`
  display: flex;
  padding: 2rem;
  width: 100%;
  max-width: 1600px;
  margin: 0 auto 1rem auto;
  background-color: rgb(250,250,250);
  @media only screen and (max-width: 685px) {
    flex-direction: column;
  }
`;

const ProjectDetailedDescription = styled.div`
  background-color: rgb(250,250,250);
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
  padding: 0 2.5rem;
  width: 100%;
  height: 41rem;
  font-size: 1.82rem;
  font-family: 'Play', sans-serif;
  color: rgb(25,25,25); 

  @media only screen and (max-width: 1250px) {
    height: 44.8rem;
  }

  @media only screen and (max-width: 835px) {
    height: 48rem;
  }

  @media only screen and (max-width: 685px) {
    height: 38rem;
  }

  @media only screen and (max-width: 510px) {
    
  }

  @media only screen and (max-width: 445px) {
    
  }

  @media only screen and (max-width: 425px) {
    height: 37.5rem;
  }
`;

const ProjectLogosContainer = styled.div`

  margin-top: 5px;

  & svg {
    width: 40px;
    height: 40px;

    @media only screen and (max-width: 1250px) {
      width: 35px;
      height: 35px;
    }

    @media only screen and (max-width: 835px) {
      width: 30px;
      height: 30px;
    }

    @media only screen and (max-width: 685px) {
      width: 25px;
      height: 25px;
    }

    @media only screen and (max-width: 445px) {
      width: 20px;
      height: 20px;
    }    
  }
`;

const ProjectImages = styled.img`
  position: relative;
  width: 100%;
  user-select: none;
`;

const ProjectImagesContainer = styled.div`
  flex: 1;
  background-color: rgb(250,250,250);
`;

class ProjectDetailed extends Component {

  constructor(props) {
    super(props);

    this.state = {
      projectImages: projects[this.props.selectedProject].images,
      maxIndex: projects[this.props.selectedProject].images.length - 1,
      currentIndex: 0
    }

    this.handleArrowClick = this.handleArrowClick.bind(this);
  }

  handleArrowClick(direction) {
    let newIndex;

    if (direction === "left") {
      if (this.state.currentIndex === 0) {
        newIndex = this.state.maxIndex;
      } else {
        newIndex = this.state.currentIndex - 1;
      }
    } else if (direction === "right") {
      if (this.state.currentIndex === this.state.maxIndex) {
        newIndex = 0;
      } else {
        newIndex = this.state.currentIndex + 1;
      }
    }

    this.setState({currentIndex: newIndex});
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.selectedProject !== this.props.selectedProject) {
      this.setState({
        projectImages: projects[this.props.selectedProject].images,
        currentIndex: 0,
        maxIndex: projects[this.props.selectedProject].images.length - 1
      });
    }
  }

  render() {
    const DescriptionText = <div>{projects[this.props.selectedProject]["description"][this.props.language]}</div>;
    const Logos = <React.Fragment>{projects[this.props.selectedProject].logos}</React.Fragment>;
    const Image = (
      <ProjectImagesContainer>
        <ProjectImages src={this.state.projectImages[this.state.currentIndex]} alt="project image" />
        <div>
        <ImageArrow direction="left" handleArrowClick={this.handleArrowClick} />
        <ImageArrow direction="right" handleArrowClick={this.handleArrowClick} />
        </div>
      </ProjectImagesContainer>
      );

    return ( 
      <ProjectDetailedContainer>
        {Image}
        <ProjectDetailedDescription>
          {DescriptionText}
          <ProjectLogosContainer>
            {Logos}
          </ProjectLogosContainer>
        </ProjectDetailedDescription>
      </ProjectDetailedContainer>
    );
  }
}

export default ProjectDetailed;