import React, { Component } from 'react';
import styled from 'styled-components';
import ProjectDetailed from './ProjectDetailed';
import Project1Pic from '../assets/Reactweather1s.jpg';
import Project2Pic from '../assets/Invader1s.jpg';
import Project3Pic from '../assets/Socialboost1s.jpg';
import Project4Pic from '../assets/Reactreadlist1s.jpg';
import Project5Pic from '../assets/Lanzaroteoverview1s.jpg';
import Project6Pic from '../assets/Flightbooker1s.jpg';


const selectedStyles = 
  "color: rgba(25,25,25); -o-transform: translate(-50%, -50%); -ms-transform: translate(-50%, -50%); -moz-transform: translate(-50%, -50%); -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); top: 50%; left: 50%; opacity: 1; background-color: rgba(255,255,255, .7); text-decoration: underline; text-decoration-color: rgba(255,50,50,.9);";

const ProjectsContainer = styled.section`
  background-color: rgb(245,245,245);
  width: 100%;
  padding: 15rem 2rem;
  position: relative;
  clip-path: polygon(0 2%, 100% 3%, 100% 100%, 0% 100%);

  @media only screen and (max-width: 685px) {
    padding: 15rem 0.2rem;
  }
`;

const GridContainer = styled.div`
  background-color: rgb(250,250,250);
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr; 
  width: 100%;
  max-width: 1600px;
  margin: 0 auto 2rem auto;

  @media only screen and (max-width: 685px) {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    width: 80%;
  }

  @media only screen and (max-width: 510px) {
    width: 100%;
  }

`;

const Project = styled.div`
  width: 100%;
  height: 135px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  cursor: pointer;
  box-sizing: border-box;
  border: 1px solid rgb(25,25,25);
  border-radius: 2px;

  @media only screen and (max-width: 1250px) {
    height: 115px;
  }

  @media only screen and (max-width: 835px) {
    height: 75px;
  }

  @media only screen and (max-width: 685px) {
    height: 85px;
  }

  @media only screen and (max-width: 510px) {
    height: 75px;
  }

  @media only screen and (max-width: 445px) {
    height: 60px;
  }
`;

const ProjectName = styled.button`
  position: absolute;
  font-size: 1.85rem;
  font-family: 'Play', sans-serif;
  border: 0;
  background-color: transparent;
  opacity: 0;
  top: 90%;
  left: 50%;
  padding: 15rem 15rem;
  white-space: nowrap;
  -o-transform: translate(-50%, -50%); 
  -ms-transform: translate(-50%, -50%); 
  -moz-transform: translate(-50%, -50%); 
  -webkit-transform: translate(-50%, -50%); 
  transform: translate(-50%, -50%);
  transition: all .4s, opacity 1s;
  cursor: pointer;

  @media only screen and (max-width: 685px) {
    font-size: 2.4rem;
    transition: all 1s ease-in;
  }

  &:focus {
    outline: none;
    color: rgba(25,25,25);
    background-color: rgba(255,255,255, .6);
    opacity: 1;
    top: 50%;  
  }
`;

const ProjectNameContainer = styled.div `
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: rgba(255,255,255, 0);
  transition: all .2s;
  
  &:hover {
    background-color: rgba(255,255,255, .5);

    & ${ProjectName} {
      opacity: 1;
      top: 50%;     
    }
  }
`;

const ExtraInfoText = styled.p`
  font-family: 'Play', sans-serif;
  font-size: 2rem;
  padding: 2rem;
  background-color: rgb(250,250,250);
  color: rgba(25,25,25,.7);
  margin: 0 auto;
  
`;


class Projects extends Component {
  
  constructor(props) {
    super(props);

    this.state = { 
      selectedProject: "ReactWeatherApp"
    };

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    // Remove selected styles from current selected project
    document.getElementById(this.state.selectedProject).style = null;

    // Add selected styles to the clicked project
    e.target.style = selectedStyles;

    // Update state
    this.setState({selectedProject: e.target.innerHTML.replace(/ /g, "")});
  }

  componentDidMount() {
    document.getElementById(this.state.selectedProject).style = selectedStyles;
  }

  render() {
    let ExtraInfo;

    if (this.props.language === "spa") {
      ExtraInfo = (
        <ExtraInfoText>
          Adicionalmente este portfolio ha sido diseñado y creado por mi usando React.<br /><br />
          Encuentra más de mis proyectos en mi <a rel="nofollow noopener noreferrer" target="_blank" href="https://github.com/TheOdinProject">Github</a>.
        </ExtraInfoText>
      )
    } else { 
      ExtraInfo = (
        <ExtraInfoText>
          Additionally this portfolio was designed and coded by me using React.<br /><br />
          Find more of my projects in my <a rel="nofollow noopener noreferrer" target="_blank" href="https://github.com/TheOdinProject">Github</a>.
        </ExtraInfoText>
      )
    }
    return (
      <ProjectsContainer id="projects">
          <ProjectDetailed 
            selectedProject={this.state.selectedProject}
            language={this.props.language} 
          />
        <GridContainer>
          <Project style={{backgroundImage: `url(${Project1Pic})`}}>
            <ProjectNameContainer>
              <ProjectName id="ReactWeatherApp" onClick={this.handleClick}>React Weather App</ProjectName>
            </ProjectNameContainer>
          </Project>
          <Project style={{backgroundImage: `url(${Project2Pic})`}}>
            <ProjectNameContainer>
              <ProjectName id="LifeInvader" onClick={this.handleClick}>Life Invader</ProjectName>
            </ProjectNameContainer>
          </Project>
          <Project style={{backgroundImage: `url(${Project3Pic})`}}>
            <ProjectNameContainer>
              <ProjectName id="SocialBoost" onClick={this.handleClick}>Social Boost</ProjectName>
            </ProjectNameContainer>
          </Project>
          <Project style={{backgroundImage: `url(${Project4Pic})`}}>
            <ProjectNameContainer>
              <ProjectName id="ReactReadList" onClick={this.handleClick}>React Read List</ProjectName>
            </ProjectNameContainer>
          </Project>         
          <Project style={{backgroundImage: `url(${Project5Pic})`}}>
            <ProjectNameContainer>
              <ProjectName id="LanzaroteOverview" onClick={this.handleClick}>Lanzarote Overview</ProjectName>
            </ProjectNameContainer>
          </Project>
          <Project style={{backgroundImage: `url(${Project6Pic})`}}>
            <ProjectNameContainer>
              <ProjectName id="FlightBooker" onClick={this.handleClick}>Flight Booker</ProjectName>
            </ProjectNameContainer>
          </Project>
        </GridContainer>
        {ExtraInfo}
      </ProjectsContainer>
    );
  }
}

export default Projects;