import React, { Component } from 'react';
import styled from 'styled-components';

const ArrowLeft = styled.button`
  position: relative;
  bottom: 0;
  right: 25%;
  font-size: 3rem;
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid transparent;
  padding: 0rem 1rem;
  color: rgba(55,55,55,1);
  font-weight: 1000; 
  transition: all .3s;
  cursor: pointer;

  &:hover {
    transform: scale(1.15);
  }

  &:focus {
    outline: none;
    border-bottom: 1px solid rgb(245,25,25);
  }

  &:hover:active {
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }

`;

const ArrowRight = styled.button`
  position: relative;
  bottom: 0;
  left: 25%; 
  font-size: 3rem;
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid transparent;
  padding: 0rem 1rem;
  color: rgba(55,55,55,1);
  font-weight: 1000;
  transition: all .3s;
  cursor: pointer;

  &:hover {
    transform: scale(1.15);
  }

  &:focus {
    outline: none;
    border-bottom: 1px solid rgb(245,25,25);
  }

  &:hover:active {
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }

`;

class ImageArrow extends Component {
  
  render() {
    if (this.props.direction === "left") {
      return (<ArrowLeft onClick={() => this.props.handleArrowClick("left")}>&#9664;</ArrowLeft>)
    } else { 
      return (<ArrowRight onClick={() => this.props.handleArrowClick("right")}>&#9654;</ArrowRight>)
    }
  }
}

export default ImageArrow