import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import AnchorLink from 'react-anchor-link-smooth-scroll';


const rotate = keyframes`
  from {
    transform: rotate(60deg);
  }

  to {
    transform: rotate(240deg);
  }
`;

const HeaderContainer = styled.header`
  background-color: rgb(25,25,25);
  height: 100vh;
  min-height: 50rem;
  position: relative;
  overflow: hidden;
`;

const TextContainer = styled.div`
  position: relative;
  top: 25%;
  text-align: center;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
`;

const RotatingBar = styled.div`
  width: 285rem;
  height: 55rem;
  background: rgba(255,50,50,.9);
  border-width: 1px 0 1px 0;
  border-style: solid;
  opacity: .85;
  border-color: rgb(25,25,25);
  position: absolute;
  top: 5rem;
  left: -100rem;
  animation: ${rotate} 90s linear infinite;
`;

const HeaderText = styled.p`
  display: inline-block;
  position: relative;
  top: 0;
  left: 0;
  margin-bottom: 4rem;
  letter-spacing: .3rem;
  color: rgb(240,240,240);
  font-family: 'Play', sans-serif;
  font-size: 5.8rem;
  border: .7rem solid rgb(245,245,245);
  padding: 2rem 4rem 2.5rem 4rem;
  background-color: transparent;
  font-weight: 10;
  overflow: hidden;
  white-space: nowrap;

  @media only screen and (max-width: 1250px) {
    font-size: 5.5rem;
  }

  @media only screen and (max-width: 835px) {
    font-size: 4.5rem;
  }
`;

const HeaderSpan = styled.span`
  font-size: 5.9rem;
  font-weight: 600;
  color: rgb(249,249,249);
`;

class Header extends Component {
  render() {
    if (this.props.language === "eng") {
      return (
        <HeaderContainer id="header">
          <RotatingBar />
          <TextContainer>
          <HeaderText>
              Hi, I'm <HeaderSpan>Javier Machín</HeaderSpan>.<br /> Full-stack web developer.
          </HeaderText>
          <AnchorLink href="#about" className="header-anchor">
            Learn more about me
          </AnchorLink>
          </TextContainer>
        </HeaderContainer>
      );
    } else {
      return (
        <HeaderContainer id="header">
          <RotatingBar />
          <TextContainer>
          <HeaderText>
            Hola, soy <HeaderSpan>Javier Machín</HeaderSpan>.<br /> desarrollador web full-stack.
          </HeaderText>
          <AnchorLink href="#about" className="header-anchor">
            Conóceme un poco más
          </AnchorLink>
          </TextContainer>
        </HeaderContainer>
      );
    }
  }
}

export default Header;