import React, { Component } from 'react';
import styled from 'styled-components';

const ContactContainer = styled.section`
  text-transform: uppercase;
  background-color: rgb(20,20,20);
  color: rgb(240,240,240);
  overflow: hidden;

  border-top: 1.5rem solid rgba(215,50,50,.9);
`

const ContactForm = styled.form`
  background-color: rgb(240,240,240);
  padding: 4.5rem 14rem;
  border-radius: 4px;
  border: 3px solid rgba(215,50,50,.9);
  margin: 5rem auto;
  width: 50%;

  @media only screen and (max-width: 1250px) {
    width: 55%; 
    font-size: 3rem;
  }

  @media only screen and (max-width: 835px) {
    width: 65%;
  }

  @media only screen and (max-width: 685px) {
    width: 85%;
  }
  
  & h3 {
    text-transform: none;
    font-family: 'Play', sans-serif;
    font-size: 2.5rem;
    color: rgb(65,65,65);
    margin-bottom: 5rem;
  }

`;

const ContactInput = styled.input`
  padding: 1rem 2rem;
  display: block;
  font-family: sans-serif;
  font-size: 1.8rem;
  margin: 1rem auto 0 auto;
  width: 75%;
  border-radius: 2px;
  border: 0;
  border-bottom: 3px solid transparent;

  @media only screen and (max-width: 835px) {
    width: 80%;
  }

  @media only screen and (max-width: 685px) {
    font-size: 1.5rem;
    width: 95%;
  }

  &:focus {
    outline: none;
  }

  &:focus:invalid {
    border-bottom: 3px solid rgba(215,50,50,.9);
  }

  &:focus:valid, &:valid {
    border-bottom: 3px solid rgba(65,170,50,.5);
  }

`;

const ContactTextArea = styled.textarea`
  padding: 1rem 2rem;
  display: block;
  font-family: sans-serif;
  font-size: 1.8rem;
  margin: 1rem auto 0 auto;
  width: 75%;
  border-radius: 2px;
  border: 0;
  border-bottom: 3px solid transparent;

  @media only screen and (max-width: 835px) {
    width: 80%;
  }

  @media only screen and (max-width: 685px) {
    font-size: 1.5rem;
    width: 95%;
  }

  &:focus {
    outline: none;
  }

  &:focus:invalid {
    border-bottom: 3px solid rgba(215,50,50,.9);
  }

  &:focus:valid, &:valid {
    border-bottom: 3px solid rgba(65,170,50,.5);
  }
`

const ContactSubmitBtn = styled.input`
  display: block;
  position: relative;
  font-size: 1.7rem;
  border: 1px solid rgba(215,50,50,.9);
  border-radius: 50px;
  background-color: rgb(255,255,255);
  color: rgba(215,50,50,.9);
  padding: .8rem 3.5rem;
  letter-spacing: 1px;
  margin: 3rem auto 0 auto;
  cursor: pointer;
  transition: all .2s;
  box-shadow: 0 .2rem 1rem rgba(0,0,0,.2);

  &:hover, &:focus {
    outline: none;
    transform: translateY(-2px);
    box-shadow: 0 .7rem 1rem rgba(0,0,0,.2); 
  }

  &:active {   
    transform: translateY(2px);
    box-shadow: 0 .5rem .4rem rgba(0,0,0,.2); 
  }

`;

class Contact extends Component {
  render() {
    return (
      <ContactContainer id="contact">
        <ContactForm action="https://formspree.io/javier_machin_acosta@hotmail.com" method="POST">
          <h3>{this.props.text.title}</h3>
          <ContactInput autoComplete="off" autoCorrect="off" autoCapitalize="off" spellCheck="false" required placeholder={this.props.text.name} type="text" name="name"  />
          <ContactInput autoCorrect="off" autoCapitalize="off" spellCheck="false"required placeholder={this.props.text.email} type="email" name="_replyto" />
          <ContactTextArea autoComplete="off" autoCorrect="off" autoCapitalize="off" spellCheck="false" rows="4" cols="21" required placeholder={this.props.text.message} name="message" />
          <ContactSubmitBtn type="submit" value={this.props.text.send} />
        </ContactForm>
      </ContactContainer>
    );
  }
}

export default Contact;