import React, { Component } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import styled from 'styled-components';
import EsFlag from '../assets/Spain-Flag.png';
import EnFlag from '../assets/UK-Flag.png';

const LanguageSelectionContainer = styled.div`
  position: absolute;
  left: 5px;
  font-size: 1.5rem;
  display: flex;
  align-items: center;

  & label {
    display: flex;
    align-items: center;
    color: rgb(240,240,240);
    margin: 0 5px;
    cursor: pointer;

    @media only screen and (max-width: 835px) {
      margin: 0 3px;
    }

    & > img {
      margin-left: 5px;
      width: 20px;
      height: 20px;

      @media only screen and (max-width: 835px) {
        margin-left: 3px;
        width: 14px;
        height: 14px;
      }
    }
  }

  & input {
    display: none;
  }

  & input:checked + label {
    color: rgba(215,50,50,.9);    
  }

  @media only screen and (max-width: 835px) {
    left: 1px;
  }

`;

class Navbar extends Component {
  render() {
    let LanguageSelection;

    const NavContainer = styled.nav`
      overflow: hidden;
      text-transform: uppercase;
      background-color: rgb(25,25,25);
      font-family: sans-serif;
      display: flex;
      justify-content: center;
      align-items: center;   
      position: -webkit-sticky;
      position: -moz-sticky;
      position: -ms-sticky;
      position: -o-sticky;
      position: sticky;
      top: 0;
      width: 100%;
      height: 5rem;
      box-shadow: 0 3.5px 6px rgba(0,0,0,.3);
      z-index: 10;
    `;

    if (this.props.selectedLanguage === "eng") {
      LanguageSelection = (
        <LanguageSelectionContainer>
          <input 
            onChange={() => this.props.handleLanguageChange("eng")} 
            type="radio" 
            id="eng" 
            name="language" 
            value="eng" 
            defaultChecked 
          />
          <label htmlFor="eng">En <img src={EnFlag} alt="UK flag" /></label>
          <input 
            onChange={() => this.props.handleLanguageChange("spa")} 
            type="radio" 
            id="spa" 
            name="language" 
            value="spa"
          />
          <label htmlFor="spa">Es <img src={EsFlag} alt="Spanish flag" /></label>
        </LanguageSelectionContainer>
      );
    } else if (this.props.selectedLanguage === "spa") {
      LanguageSelection = (
        <LanguageSelectionContainer>
          <input 
            onChange={() => this.props.handleLanguageChange("eng")} 
            type="radio" 
            id="eng" 
            name="language" 
            value="eng" 
          />
          <label htmlFor="eng">En <img src={EnFlag} alt="UK flag" /></label>
          <input 
            onChange={() => this.props.handleLanguageChange("spa")} 
            type="radio" 
            id="spa" 
            name="language" 
            value="spa" 
            defaultChecked 
          />
          <label htmlFor="spa">Es <img src={EsFlag} alt="Spanish flag" /></label>
        </LanguageSelectionContainer>
      );
    } 
    
    return (
      <NavContainer id="navbar">
        {LanguageSelection}
        <AnchorLink 
          style={this.props.styles.home} 
          offset='50' 
          href='#header'>
          {this.props.text.home}
        </AnchorLink>
        <AnchorLink 
          style={this.props.styles.about} 
          offset='50' 
          href='#about'>
          {this.props.text.about}
        </AnchorLink>
        <AnchorLink 
          style={this.props.styles.projects} 
          offset='50' 
          href='#projects'>
          {this.props.text.projects}
        </AnchorLink>
         <AnchorLink 
          style={this.props.styles.opensource} 
          offset='50' 
          href='#opensource'>
          {this.props.text.openSource}
        </AnchorLink>
        <AnchorLink 
          style={this.props.styles.contact} 
          offset='50' 
          href='#contact'>
          {this.props.text.contact}
        </AnchorLink>
      </NavContainer>
    );
  }
}

export default Navbar;