import React, { Component } from 'react';
import './css/App.css';
import styled from 'styled-components';
import { handleScroll, updateOffsets} from './lib/handleScroll';
import Header from './components/Header';
import Navbar from './components/Navbar';
import About from './components/About';
import Technologies from './components/Technologies';
import Projects from './components/Projects';
import OpenSource from './components/OpenSource';
import Contact from './components/Contact';
import Text from './data/text';

const MainContainer = styled.div`
  text-align: center;
  color: rgb(25,25,25);
  font-family: sans-serif;
  font-size: 3rem;
  background-color: rgba(215,50,50,.9);

  a {
    color: rgba(185,50,50,.9);
    text-decoration: none;
  }
`;

const FooterText = styled.p `
  display: block;
  position: relative;
  bottom: 0;
  font-size: 1.5rem;
  background-color:rgba(175,25,25,.9);
  color: white;
`;

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      language: "eng", 
      styles: {
        home: {},
        about: {},
        projects: {},
        opensource: {},
        contact: {}
      }
    };

    this.handleScroll = handleScroll.bind(this);
    this.handleLanguageChange = this.handleLanguageChange.bind(this);
  }

  componentDidMount() {    
    updateOffsets();
    window.addEventListener('scroll', () => {
      this.handleScroll();
    });
  }

  handleLanguageChange(clickedLanguage) {
    this.setState({language: clickedLanguage});
  }

  render() {
    return (
      <MainContainer>
        <Header language={this.state.language} />
        <Navbar 
          navPosition={this.state.navPosition}
          styles={this.state.styles}
          text={Text[this.state.language].navbar} 
          selectedLanguage={this.state.language}
          handleLanguageChange={this.handleLanguageChange} />
        <About 
          text={Text[this.state.language].about} 
          aboutHeight={this.state.aboutHeight} 
          aboutPadding={this.state.aboutPadding} 
        />
        <Technologies language={this.state.language} />
        <Projects language={this.state.language} />
        <OpenSource text={Text[this.state.language].opensource} />
        <Contact text={Text[this.state.language].contact} />
        <FooterText>© 2018 Javier Machín</FooterText>
      </MainContainer>
    );
  }
}

export default App;